.app-wrapper {
  height: 100%;
  margin: 0 auto;
  padding: 0;
  width: 1200px;
  display: grid;
  grid-template-areas:
    "h h"
    "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  /* grid-gap: 10px; */
}

.app-wrapper-content {
  background-color: #bad5dd;
  grid-area: c;
}
